export const displayLevel = (level: string | number) => {
    switch (level) {
        case 0:
            return `Lv.${level} `;
        case 1:
            return `Lv.${level} `;
        case 2:
            return `Lv.${level} `;
        case 3:
            return `Lv.${level} `;
        case 4:
            return `Lv.${level} `;
        case 5:
            return `Lv.${level}  `;
        case 6:
            return `Lv.${level} `;
        default:
            return `Lv.${level} `;
    }
};
