import { getProfile } from "@/apis/profile";
import MainButton from "@/components/Button";
import { loadingState } from "@/storage/recoil/loadingState";
import { modalState } from "@/storage/recoil/modalState";
import { userState } from "@/storage/recoil/userState";
import { useEffect, useState } from "react";
import { useQueries } from "react-query";
import { useRecoilState } from "recoil";
import { getPortfolio } from "../../apis/profile/index";
import AboutMeLayout from "./Layout/AboutMeLayout";
import CommentLayout from "./Layout/CommentLayout";
import LectureHistoryLayout from "./Layout/LectureHistoryLayout";
import ReviewHistoryLayout from "./Layout/ReviewHistoryLayout";
import ScoreLayout from "./Layout/ScoreLayout";
import SubContentLayout from "./Layout/SubContentLayout";
import {
    MypageContentLayout,
    MypageLayoutContainer,
} from "./ProfilepageLayout.style";
const ProfilePageLayout = () => {
    const [loading, setLoading] = useRecoilState(loadingState);
    const [modalVisible, setModalVisible] = useRecoilState(modalState);
    const [userInfo, setUserInfo] = useRecoilState(userState);
    const [myProfile, setProfile] = useState(null);

    const result = useQueries([
        {
            queryKey: ["my-portfolio"],
            queryFn: () => getPortfolio(),
            onSuccess: (data: any) => {
                setProfile(data);
            },
        },

        {
            queryKey: ["getUserinfo"],
            queryFn: () => getProfile(),
            onSuccess: (data: any) => {
                setUserInfo(data);
            },
        },
    ]);
    useEffect(() => {
        setLoading(result.some((result) => result.isLoading));
    }, [result]);
    useEffect(() => {
        setModalVisible(false);
    }, []);

    const UnsmileEmoji = "../../assets/unsmile_emoji.png";
    const coments: any[] = [
        {
            day: "8",
            score: 5,
            content: "",
            emoji: UnsmileEmoji,
        },
        {
            day: "8",
            score: 5,
            content: "",
            emoji: UnsmileEmoji,
        },
        {
            day: "8",
            score: 2,
            content: "",
            emoji: UnsmileEmoji,
        },
        {
            day: "8",
            score: 3,
            content: "",
            emoji: UnsmileEmoji,
        },
    ];
    const review = {
        isOwner: true,
        title: " ",
        year: "2020",
        semester: "1",
        createdAt: "2022-12-12", //

        writerId: "29292",
        nickname: "111",
        writerLevel: 2,
        memberNum: 4, //
        startDate: "1",
        endDate: "2",
        reviewLevel: "",

        subject: "",
        detail: "",
        files: [],
    };

    const comment = {
        canWrite: true, //   &  

        comments: [
            {
                isLike: true,
                year: "2022",
                semester: "1",
                createdAt: "2022-02-01 08:12:40", //yyyy-mm-dd hh:mm:ss 
                comment: "",
            },
            {
                isLike: false,
                year: "2022",
                semester: "1",
                createdAt: "2022-01-01 12:40:00",
                comment: "",
            },
            {
                isLike: true,
                year: "2022",
                semester: "1",
                createdAt: "2022-12-11 12:40:00",
                comment: "",
            },
            {
                isLike: false,
                year: "2022",
                semester: "1",
                createdAt: "2022-12-01 12:40:00",
                comment: "",
            },
        ],
    };
    const [tab, setTab] = useState("main");

    const getTabPage = () => {
        switch (tab) {
            case "main":
                return (
                    <>
                        <MypageContentLayout>
                            <AboutMeLayout
                                {...result[0].data}
                                nickname={userInfo?.nickname}
                            />
                            <ScoreLayout coments={coments} />
                            <SubContentLayout {...result[0].data} />
                            <CommentLayout {...comment} reviewId={0} />
                        </MypageContentLayout>
                    </>
                );
            case "review":
                return <ReviewHistoryLayout />;

            case "recruit":
                return <LectureHistoryLayout />;
        }
    };

    return (
        <>
            {userInfo !== null && myProfile !== null ? (
                <MypageLayoutContainer>
                    <MypageContentLayout>
                        <AboutMeLayout
                            {...result[0].data}
                            nickname={userInfo?.nickname}
                        />
                        <ScoreLayout coments={coments} />
                        <SubContentLayout {...result[0].data} />
                        <div className="review-btn">
                            <MainButton
                                width="100%"
                                clickListener={() => {
                                    setModalVisible(true);
                                }}
                            >
                                 
                            </MainButton>
                        </div>
                        {/* <CommentLayout {...comment} reviewId={0} /> */}
                    </MypageContentLayout>
                </MypageLayoutContainer>
            ) : null}
        </>
    );
};

export default ProfilePageLayout;
